import React from 'react';

import { Menu, Icon} from 'antd';

import { Link } from 'react-router-dom';

const { SubMenu } = Menu;

class System extends React.Component{
 constructor(...args)
 {
     super(...args)
     this.state = {
         arr:['sub1'],
         index:''
     }
 }
 fn(e)
 {
     
     let arr = [];
     arr.push(e.key)
     if(arr[0] == this.state.arr[0])
     {
         arr=[];
     }
     this.setState({
         arr:arr
     })
 }
 fnn(e)
 {
     this.setState({
        index:e.key
     })
 }
    render(){
    const str =  <Menu
    openKeys={this.state.arr}
    mode="inline"
    onClick={this.fnn.bind(this)}
    style={{ height: '100%' }}
   >
   <SubMenu onTitleClick={this.fn.bind(this)} key='sub1' title={<span><Icon type="credit-card" /><span></span></span>}>
       <Menu.Item><Link to='auto_pay'></Link></Menu.Item>
       <Menu.Item>VIP</Menu.Item>
       <Menu.Item><Link to='get_point_card_log'></Link></Menu.Item>
       <Menu.Item></Menu.Item>
   </SubMenu>
   <SubMenu onTitleClick={this.fn.bind(this)} key='sub2' title={<span><Icon type="pay-circle" /><span></span></span>}>
       <Menu.Item><Link to='borrow_money'></Link></Menu.Item>
       <Menu.Item><Link to='payback'></Link></Menu.Item>
       <Menu.Item><Link to='loan'></Link></Menu.Item>
       <Menu.Item><Link to='balance_excheng_log'></Link></Menu.Item>
   </SubMenu>
   <SubMenu onTitleClick={this.fn.bind(this)} key='sub3' title={<span><Icon type="search" /><span></span></span>}>
       <Menu.Item><Link to='records'></Link></Menu.Item>
       <Menu.Item><Link to='exchange'></Link></Menu.Item>
       <Menu.Item></Menu.Item>
   </SubMenu>
    </Menu>
      return(
        <div>
         {str}
       </div>
        )
    }
}


export default  System;
